export default connect(
  state => state,
  dispatch => bindActionCreators(action, dispatch),
  ({ game, history }, { enter, leave }, { onReset }) => {
    const reset = () => {
      leave()
      enter()
      onReset()
    }

    return {
      reset: !game.isPlaying && !!history.length ? reset : undefined
    }
  }
)(NewGame)
